<template>
  <div class="form" @click="toggle = true; name=''">
    <div v-on:click.stop>
      <div>
        <div class = "btn-create-header" v-if="toggle" @click="toggle = false">
          <b-icon icon="plus" font-scale="1"></b-icon> Add more
        </div>
      </div>
      <div v-if="!toggle" class="create-group">
        <b-form-textarea
          class="input-create-form"
          rows="3"
          max-rows="6"
          v-model="componentName"
          placeholder="name"
        ></b-form-textarea>
        <b-button class="btn-creator btn-create-submit" variant="success" @click="onCreate">Submit</b-button>
        <b-button class="btn-creator btn-cancel-submit" variant="danger" @click="toggle = true; name=''">Cancel</b-button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "CreatorPopUp",

    props: {
    },

    data() {
      return {
        toggle: true,
        componentName: ""
      }
    },

    methods: {
      onCreate: function() {
        this.$emit("onSubmit", this.componentName);
        this.toggle = true;
        this.componentName = "";
      }
    }

  }
</script>

