let UNAUTHORIZED = 401;
let BAD_REQUEST  = 400;
let UNAVAILABLE_FOR_LEGAL_REASONS = 451;

import SignOut from '../SignOut.js';
import RouterUtil from '../../router/RouterUtil.js';

class BoardErrorHandler {
  constructor(page) {
    this.page = page;
    this.buildHandler();
  }

  buildHandler() {
    this.handlers = {};

    this.handlers[UNAUTHORIZED] = this.onReceiveUnauthorized.bind(this);
    this.handlers[BAD_REQUEST] = this.onReceiveBagQuest.bind(this);
    this.handlers[UNAVAILABLE_FOR_LEGAL_REASONS] = this.onReceiveReloadPage.bind(this);
  }

  onError(error, callback) {
    let response = error.response.data;
    console.log("BoardErrorHandler " + JSON.stringify(error));

    let status = response.status;

    this.handlers[status] && this.handlers[status](response);
    !this.handlers[status] && this.onReceiveCommonError(response);

    callback && callback(error);
  }

  onReceiveUnauthorized(response) {
    console.log("BoardErrorHandler -> onReceiveUnauthorized " + JSON.stringify(response));
    this.onCreateNotification("Unauthorized", response, "danger");
    SignOut.signOut(this.page);
  }

  onReceiveBagQuest(response) {
    console.log("BoardErrorHandler -> onReceiveBagQuest " + JSON.stringify(response));
    this.onCreateNotification("Bag request", response, "warning");
  }

  onReceiveReloadPage(response) {
    console.log("BoardErrorHandler -> onReceiveReloadPage " + JSON.stringify(response));
    RouterUtil.reload(this.page);
  }

  onReceiveCommonError(response) {
    console.log("BoardErrorHandler -> onReceiveCommonError " + JSON.stringify(response));
    this.onCreateNotification("Error", response);
  }

  onCreateNotification(title, response, variant = "danger") {
    const h = this.page.$createElement;
    let message =
    h(
      'div',
      [
        h('b-spinner', { props: { type: 'grow', small: true } }),
        '[PATH]: ' + response.path,
        h('br'),
        h('br'),
        h('b-spinner', { props: { type: 'grow', small: true } }),
        '[MESSAGE]: ' + response.message
      ]
    );
    this.page.$bvToast.toast(
      [message],
      {
        variant: variant,
        title: title + ": " + response.status ,
        autoHideDelay: 5000,
        appendToast: false
      }
      );
  }

}

export default BoardErrorHandler;
