<template>
  <div class="quick-bag-editor" @click="onClickCancel">
    <div v-on:click.stop class="quick-bag-editor-bag" :style="position">
      <b-form-textarea
        class="text-area-bag"
        rows="3"
        max-rows="6"
        v-model="data.value"
        placeholder="name"
      ></b-form-textarea>

      <b-button @click="onClickSave" class="btn-submit-edit-bag btn-save">Save</b-button>
      <b-button @click="onClickDelete" class="btn-submit-edit-bag btn-delete">Delete</b-button>
    </div>
  </div>
</template>

<script>
  export default {
    name: "BagPopUp",
    props: {
      data: {
        value: {
          type: String,
          default: ""
        },
        bagId: {
          type: Number,
          default: -1
        },
        index: {
          type: Number,
          default: -1
        },
        rect: null
      }
    },

    computed: {
      position: function() {
        let position = {
          left: (this.data.rect != null ? this.data.rect.left : 0) + 'px',
          top: (this.data.rect != null ? this.data.rect.top : 0) + 'px',
          // width: (this.data.rect != null ? this.data.rect.width : 0) + 'px',
          // height: (this.data.rect != null ? this.data.rect.height : 0) + 'px',
        };
        return position;
      }
    },

    data() {
      let self = this;
      return {
        SAVE: "onSave",
        CANCEL: "onCancel",
        DELETE: "onDelete"
      }
    },
    methods: {

      onClickSave: function() {
        let self = this;
        console.log(self.data.rect);
        console.log("BagPopUp -> onClickSave: " + self.data.bagId + " | " + self.data.value);
        this.$emit(self.SAVE, self.data.bagId, self.data.value);
      },

      onClickCancel: function() {
        let self = this;
        this.$emit(self.CANCEL);
      },

      onClickDelete: function() {
        let self = this;
        this.$emit(self.DELETE, self.data.bagId, self.data.index);
      }
    }
  }
</script>

