import RouterUtil from '../../router/RouterUtil.js';
import WsCmd from '../../config/WebsocketCmd.js';

class BoardWebsocketHandler {

  constructor(page) {
    this.page = page;
    this.buildHandler();
  }

  buildHandler() {
    this.handlers = {};

    this.handlers[WsCmd.ADD_MEMBER]     = this.receiveMessageAddMember.bind(this);
    this.handlers[WsCmd.LEAVE_BOARD]     = this.receiveMessageLeaveBoard.bind(this);

    this.handlers[WsCmd.UPDATE_BOARD]     = this.receiveMessageUpdateBoard.bind(this);
    this.handlers[WsCmd.DELETE_BOARD]     = this.receiveMessageDeleteBoard.bind(this);

    this.handlers[WsCmd.CREATE_BAG]     = this.receiveMessageCreateBag.bind(this);
    this.handlers[WsCmd.MOVE_BAG]       = this.receiveMessageMoveBag.bind(this);
    this.handlers[WsCmd.DELETE_BAG]     = this.receiveMessageDeleteBag.bind(this);
    this.handlers[WsCmd.EDIT_BAG]       = this.receiveMessageEditBag.bind(this);

    this.handlers[WsCmd.CREATE_CARD]    = this.receiveMessageCreateCard.bind(this);
    this.handlers[WsCmd.MOVE_CARD]      = this.receiveMessageMoveCard.bind(this);
    this.handlers[WsCmd.DELETE_CARD]    = this.receiveMessageDeleteCard.bind(this);
    this.handlers[WsCmd.EDIT_CARD]      = this.receiveMessageEditCard.bind(this);
  }

  onmessage(event) {
    console.log(`BoardWebsocketHandler -> onMessage ${event.data}`);
    let data = JSON.parse(event.data);
    if (data.boardId != this.page.boardId) return;
    this.handlers[data.action](data.message);
  }

  onclose(event) {
    this.page.loading = 0;
  }

  onerror(error) {

  }

  receiveMessageAddMember(message) {
    console.log("BoardWSHandler -> receiveMessageCreateBag " + JSON.stringify(message));
    let member = message;
    this.page.members.push(member);
  }

  receiveMessageLeaveBoard(message) {
    console.log("BoardWSHandler -> receiveMessageLeaveBoard " + JSON.stringify(message));
    let memberId = message;
    if (this.page.currentUser.id == memberId) {
      RouterUtil.redirectToHomeFrom(this.page);
      return;
    }
    let members = this.page.members;
    for (let i = 0; i < members.length; i++) {
      if (members[i].id == memberId) {
        members.splice(i, 1);
        break;
      }
    }
  }

  receiveMessageUpdateBoard(message) {
    console.log("BoardWSHandler -> receiveMessageUpdateBoard " + JSON.stringify(message));
    this.page.boardName = message;
    this.page.newName = "";
  }

  receiveMessageDeleteBoard(message) {
    console.log("BoardWSHandler -> receiveMessageDeleteBoard " + JSON.stringify(message));
    RouterUtil.redirectToHomeFrom(this.page);

  }

  receiveMessageCreateBag(message) {
    console.log("BoardWSHandler -> receiveMessageCreateBag " + JSON.stringify(message));
    let newBag = Object.values(message)[0];
    let page = this.page;

    let bag = {
      id: newBag.id,
      name: newBag.name,
      position: newBag.position,
      cards: []
    };
    this.page.board.bags.push(bag);
    this.page.bagById[bag.id] = bag; // map oneToOne
    this.page.board.bags.sort((thisBag, thatBag) => {return thisBag.position - thatBag.position;});
  }

  receiveMessageMoveBag(message) {
    console.log("BoardWSHandler -> receiveMessageMoveBag " + JSON.stringify(message));
    let movedBags = Object.values(message);
    let page = this.page;
    movedBags.forEach(movedBag => {
      let bag = page.bagById[movedBag.id];
      bag.position = movedBag.position;
    });

    page.board.bags.sort((a, b) => {return a.position - b.position;});
  }

  receiveMessageDeleteBag(message) {
    console.log("BoardWSHandler -> receiveMessageDeleteBag " + JSON.stringify(message));
    let bagId = message;
    let bag = this.page.bagById[bagId];
    let bags = this.page.board.bags;
    let cards = bag.cards;
    let page = this.page;

    cards.forEach(card => {
      delete page.cardById[card.id];
    });

    delete page.bagById[bagId];

    let index = 0;
    for (index; index < bags.length; index++) {
      if (bags[index].id == bagId) break;
    }
    bags.splice(index, 1);
  }

  receiveMessageEditBag(message) {
    console.log("BoardWSHandler -> receiveMessageEditBag " + JSON.stringify(message));

    this.page.bagById[message.bagId].name = message.name;
  }

  receiveMessageCreateCard(message) {
    console.log("BoardWSHandler -> receiveMessageCreateCard " + JSON.stringify(message));
    let newCard = Object.values(message)[0];
    this.page.cardById[newCard.id] = newCard;

    this.page.board.bags.forEach(bag => {
      if (bag.id == newCard.bagId) {
        bag.cards.push(newCard);
        bag.cards.sort((c1, c2) => {
          return c1.position - c2.position;
        });
        console.log("BoardWSHandler -> receiveMessageCreateCard: " + JSON.stringify(this.page.board.bags));
        return;
      }
    });
  }

  /**
   * {"58":{"id":58,"bagId":79,"name":"edit name","position":360442.5}}
   * @param message
   */
  receiveMessageMoveCard(message) {
    console.log("BoardWSHandler -> receiveMessageMoveCard " + JSON.stringify(message));
    let reqCardId = message.reqCardId;
    let reqCard = message.modifiedCards[reqCardId];

    let movedCards = Object.values(message.modifiedCards);

    let card = this.page.cardById[reqCardId];
    let oldBag = this.page.bagById[card.bagId];
    let oldCards = oldBag.cards;

    for (let i = 0; i < oldCards.length; i++) {
      if (oldCards[i].id == reqCardId) {
        oldCards.splice(i, 1);
        break;
      }
    }

    let newBag = this.page.bagById[message.toBagId];
    let newCards = newBag.cards;

    for (let i = 0; i < newCards.length; i++) { // double check for screen making request
      if (newCards[i].id == reqCardId) {
        newCards.splice(i, 1);
        break;
      }
    }

    card.bagId = message.toBagId;
    newCards.push(card);

    let self = this;
    movedCards.forEach(movedCard => {
      self.page.cardById[movedCard.id].position = movedCard.position;
    });


    newCards.sort((c1, c2) => {
      return c1.position - c2.position;
    });

    console.log("debug0 " + JSON.stringify(newBag) + "\n" + JSON.stringify(oldBag));
    console.log("debug1 " + JSON.stringify(newCards) + "\n" + JSON.stringify(oldCards));
  }

  receiveMessageDeleteCard(message) {
    console.log("BoardWSHandler -> receiveMessageDeleteCard " + JSON.stringify(message));
    let bagId = message.bagId;
    let cardId = message.cardId;
    // self.board.bags[bagIndex].cards.splice(cardIndex, 1);
    let i = 0;
    let bags = this.page.board.bags;
    for (i; i < bags.length; i++) {
      if (bags[i].id = bagId) break;
    }
    let inBag = bags[i];
    for (let j = 0; j < inBag.cards.length; j++) {
      if (inBag.cards[j].id == cardId) {
        inBag.cards.splice(j, 1);
        break;
      }
    }

    delete self.cardById[cardId]; // remove OneToOne
  }

  receiveMessageEditCard(message) {
    console.log("BoardWSHandler -> receiveMessageEditCard " + JSON.stringify(message));
    this.page.cardById[message.cardId].name = message.name;
  }
}

export default BoardWebsocketHandler;
