<template>
  <div style="width: 100%">
    <div v-if="loading < 2" class="loading-background">
      <img class="loading-item" src="../../assets/loading-gear-curving.gif"/>
    </div>
    <div v-if="loading >= 2">
      <b-container>

        <b-button style="margin: 20px" variant="light" @click="showCreateBoardArea = !showCreateBoardArea">Create bag</b-button>

        <div v-if="showCreateBoardArea" class="create-bag-area">

          <div class="create-bag-layer" @click="showCreateBoardArea = false"></div>

          <div class="create-bag-form">
            <b-form-textarea
              v-model="newBoardName"
              rows="3"
              max-rows="6"
              placeholder="name"
            >

            </b-form-textarea>
            <b-button style="margin-top: 15px" @click="createBoard" class="btn-submit-edit-bag btn-save" variant="info">Save</b-button>
            <b-button style="margin-top: 15px; margin-left: 15px" @click="showCreateBoardArea = false" class="btn-submit-edit-bag btn-save" variant="danger">Cancel</b-button>
          </div>
        </div>

        <div class="portfolio-grid">

          <!-- Portfolio Item 1 -->
          <figure v-for="board in boards">
            <a :href="'/board/' + board.id">
              <img :src="require('../../assets/portfolio/' + ((board.id % 12) + 1)  + '.jpg')" alt="">
              <h5 class="myh5">{{board.name}}</h5>
              <small>{{board.id}}</small>
              <div>
                <!--<h5 class="name">{{board.name}}</h5>-->
              </div>
            </a>
          </figure>
        </div>

        <!--PRE RELOAD IMAGE-->
        <div v-show="false" v-for="index in 12">
          <img :src="require('../../assets/portfolio/' + ((index % 12) + 1)  + '.jpg')" alt="">
        </div>

      </b-container>

    </div>
  </div>
</template>

<script>
  // @ is an alias to /src
  import 'firebase/auth';
  import script from './Home'
  import httpStatus from '../../network/HttpStatus';
  import RouterUtil from "../../router/RouterUtil";
  import ENDPOINT from '../../router/endpoint';
  import AutoResponsive from 'autoresponsive-vue'
  import SignOut from '../SignOut.js';
  import CreatorPopUp from "../board/components/CreatorPopUp";
  import HomeWebsocketHandler from "./HomeWebsocketHandler.js";

  import MyWebsocket from "../../network/MyWebsocket";
  import NetworkConfig from "../../config/NetworkConfig.js";
  import storage from '../../storage/LocalPersistence.js';

  export default {
    name: 'Home',
    components: {CreatorPopUp, AutoResponsive},

    mounted() {
      this.fetchBoards();
      this.connectWebsocket();
    },

    beforeDestroy() {
      this.homeWS.close();
    },

    data() {
      return {
        boards: [],
        showCreateBoardArea: false,
        newBoardName: "",
        loading: 0,
        currentUserId: null,


        homeWS: new MyWebsocket(NetworkConfig.WS_URL, this),
        WSHandler: new HomeWebsocketHandler(this)
      }
    },

    computed: {
      boardGrids: function() {
        let colSize = 3;
        this.boards.sort((thisBoard, thatBoard) => {return thatBoard.id - thisBoard.id});
        return this.boards.reduce(function (rows, key, index) {
          return (index % colSize == 0 ? rows.push([key])
            : rows[rows.length-1].push(key)) && rows;
        }, []);
      }
    },

    methods: {
      connectWebsocket: function() {
        let jwt = storage.getJwt();
        this.homeWS.setJwt(jwt);
        this.homeWS.connect();
      },

      createBoard: function () {
        this.showCreateBoardArea = false;
        let self = this;

        let data = {
          boardName: self.newBoardName
        };

        self.newBoardName = "";

        script.createBoard(data,
          (response) => {
            console.log("Home -> createBoard. " + JSON.stringify(response));
            let status = response.status;
            if (status == httpStatus.OK) {
              console.log("Home -> createBoard. Success: " + JSON.stringify(response.data));
              self.boards.unshift(response.data);
            }
          },
          (error) => {
            console.log("Home -> createBoard fail. " + JSON.stringify(error));
          }
        )
      },

      fetchBoards: function () {
        let self = this;
        script.fetchBoards(
          (response) => {
            console.log("Home -> fetchBoards. " + JSON.stringify(response));
            let status = response.status;
            if (status == httpStatus.OK) {
              self.boards = script.mapToBoards(response.data.boards);
              self.currentUserId = response.data.userId;
              console.log("Home -> fetchBoards status success: " + JSON.stringify(self.boards));
              self.loading += 1;
            } else {
              console.log("Home -> fetchBoards status fail");
            }
          },
          (error) => {
            console.log("Home -> fetchBoards error. " + JSON.stringify(error.response));
            if (error.response.status == 401) {
              SignOut.signOut(self);
            }
          }
        );
      },

      onClickBag: function(boardId) {
        RouterUtil.redirect(this, ENDPOINT.BOARD + "/" + boardId);
      },


      /**
       * ============
       * Websocket
       * ============
       */

      onopen(event) {
        this.loading += 1;
      },

      onmessage(event) {
        this.WSHandler.onmessage(event);
      },

      onclose(event) {
        this.WSHandler.onclose(event);
      },

      onerror(error) {
        this.WSHandler.onerror(error);
      }
    }
  }
</script>
