<template>
  <div class="container">
    <div v-if="loading" class="loading-background">
      <img class="loading-item" src="../assets/loading-gear-curving.gif"/>
    </div>
    <div v-if="!loading">
      <div class="page-sign-in-background"></div>
      <div class="sign-in-background">
        <div class="page-sign-in-icon">
          <!--<b-icon icon="person" font-scale="8" variant="outline-secondary"></b-icon>-->
        </div>
      </div>

      <div class="form-sign-in">
        <div style="cursor: not-allowed;">
          <div class="form-input">
            <b-input disabled style="cursor: not-allowed; margin-bottom: 20px" v-model="email" type="email" placeholder="email"></b-input>
            <b-input disabled style="cursor: not-allowed;" type="password" placeholder="password"></b-input>
          </div>
          <div class="form-btn">
            <b-button @click="basicSignIn" variant="outline-secondary" disabled style="cursor: no-drop;">Sign in</b-button>
            <!--<b-button style="margin: 5px" @click="basicSignUp" variant="outline-success">Sign up</b-button>-->
          </div>
        </div>
        <div class="form-social" style="text-align: center">
          <img class="social-btn" style="width: 42px; height: 42px;" src="../assets/facebook-icon.svg"
          @click="onSocialLogin('facebook')"
          >
          <img class="social-btn" style="width: 42px; height: 42px;" src="../assets/google-icon.svg"
               @click="onSocialLogin('google')"
          >
        </div>
      </div>
      <br>
    </div>
  </div>
</template>

<script>
  import firebase from 'firebase/app';
  import ProviderConfig from '../config/firebase/ProviderConfig';
  import script from './SignIn';
  import httpStatus from '../network/HttpStatus';
  import storage from '../storage/LocalPersistence';
  import RouterUtil from '../router/RouterUtil';


  export default {
    name: "SignIn",
    data() {
      return {
        email: '',
        password: 'password',
        displayedName: 'email',

        ProviderConfig: ProviderConfig,
        loading: false
      }
    },

    methods: {
      basicSignIn: function() {
        let self = this;
        let data = {
          email: self.email,
          password: self.password
        };

        script.basicSignIn(data,
          (response) => {
            console.log("basicSignIn success: " + JSON.stringify(response));
            let status = response.status;
            if (status == httpStatus.OK) {
              self.onSuccess(response);
            } else {
              self.onFail(response);
            }
          },
          (error) => {
            console.log("basicSignIn fail: " + JSON.stringify(error));
          }
        );
      },

      basicSignUp: function() {
        let self = this;
        let data = {
          email: self.email,
          password: self.password,
          displayedName: self.displayedName
        };

        script.basicSignUp(data,
          (response) => {
            console.log(status + " basicSignUp success: " + JSON.stringify(response));
            let status = response.status;
            if (status == httpStatus.OK) {
              self.onSuccess(response);
            } else {
              self.onFail(response);
            }
          },
          (error) => {
            console.log("basicSignUp fail: " + JSON.stringify(error));
          }
        );
      },

      onSuccess: function(response) {
        let data = response.data;
        let jwt = data.jwt;

        storage.setJwt(jwt, this.goToHome);
      },

      onFail: function(response) {
        console.log(JSON.stringify(response));
      },

      onSocialLogin: function(providerName) {

        const provider = new ProviderConfig[providerName]['provider']();
        let scopes = ProviderConfig[providerName]['scopes'];
        scopes.forEach((scope) => {
          provider.addScope(scope);
        });

        let self = this;

        firebase.auth().signInWithPopup(provider)
          .then(
            (result) => {
              self.loading = true;
              firebase.auth().currentUser.getIdToken(true).then(
                token => {
                  console.log("token " + token);
                  storage.setSocialJwt(token, self.socialLoginToServer.bind(self, token));
                }
              )
            }
          ).catch((error) => {
              alert("error " + error.message);
              self.loading = false;
          });
      },

      socialLoginToServer(jwt) {
        script.socialLogin(jwt,
          (response) => {
            console.log("socialLoginToServer SUCCESS " + JSON.stringify(response));
            this.onSuccess(response);
          },
          (error) => {
            console.log("socialLoginToServer ERROR " + JSON.stringify(error));
            this.onFail(error);
          }
        )
      },

      goToHome() {
        RouterUtil.redirectToHomeFrom(this);
      }

    }
  }
</script>

