<template>
  <div>
    <div v-if="loading < loadingItem" class="loading-background">
      <img class="loading-item" src="../../assets/loading-gear-curving.gif"/>
    </div>
    <div v-if="loading >= loadingItem" class="demo">
      <div class="board-member">
        <div style="display: inline">
          <b-avatar
            icon="info-circle"
            style="cursor: pointer"
            :variant="widget.sidebar_btn.variant"
            class="mr-3"
            @mouseover="widget.sidebar_btn.variant='secondary'"
            @mouseleave="widget.sidebar_btn.variant='outline-secondary'"
            v-b-toggle="'board-sidebar'"
          >
          </b-avatar>

          <b-avatar
            :icon="widget.members_btn.icon"
            style="cursor: pointer"
            variant="secondary"
            @mouseover="widget.members_btn.icon='people'"
            @mouseleave="widget.members_btn.icon='people-fill'"
            class="mr-3"
            v-b-toggle="'member-collapse'"
          ></b-avatar>
          <b-avatar
            @click="showAddMemberInput = !showAddMemberInput"
            :icon="widget.add_member_btn.icon"
            @mouseover="widget.add_member_btn.icon='person-plus'"
            @mouseleave="widget.add_member_btn.icon='person-plus-fill'"
            style="cursor: pointer"
            variant="light"
            class="mr-3"
            size="2rem"
          ></b-avatar>
          <transition name="add-member-input-transition">
            <input
              v-show="showAddMemberInput"
              style="border: 5px; padding: 7px; background-color: #eeeeee; border-radius: 5px;"
              type="email"
              v-model="addedEmail"
              placeholder="Enter email..."
              @keyup.enter="addMemberByEmail">
          </transition>
        </div>

        <b-sidebar id="board-sidebar"
                   backdrop-variant="danger"
                   backdrop="true"
                   shadow
        >
          <div>
            <h3 style="text-align: center">{{boardName}}</h3>
            <img
              style="width: 100%; height: auto;"
              :src="require('../../assets/portfolio/' + ((boardId % 12) + 1)  + '.jpg')"
            />

            <div style="margin: 5%;">
              <b-badge>Enter new board name</b-badge>
              <b-input
                style="margin-top: 1%; width: 90%"
                placeholder="Enter new board name"
                v-model="newName"
                @keyup.enter="updateBoard"
              >
              </b-input>
            </div>

            <div style="margin-top: 10%" v-if="currentUser.boardRole=='MOD'">
              <b-icon
                :icon="widget.sidebar_btn.delete_board_icon"
                @mouseover="widget.sidebar_btn.delete_board_icon='x-circle-fill'"
                @mouseleave="widget.sidebar_btn.delete_board_icon='x-circle'"
                scale="4"
                style="margin-left: 10%; cursor: pointer"
                variant="danger"
                @click="deleteBoard"
              >
              </b-icon>
              <b-badge style="margin-left: 8%;" variant="outline-danger">Remove board</b-badge>
            </div>

          </div>
        </b-sidebar>


        <b-collapse id="member-collapse">
          <b-list-group style="width: 300px;">
            <b-list-group-item class="d-flex align-items-center" v-for="member in members" :key="member.id">
              <b-avatar class="mr-3" :src="member.avatarUrl"></b-avatar>
              <div>
                <b-badge :variant="member.boardRole === 'MOD' ? 'warning' : 'secondary'">{{member.boardRole}}</b-badge>
                <span style="margin-left: 10px" class="mr-auto">{{(member.displayedName.length > 10) ? member.displayedName.substr(0, 10) + ' ...' : member.displayedName}}</span>
                <b-icon @click="removeMember(member.id)" v-if="member.boardRole === 'MEMBER' && currentUser.boardRole == 'MOD'" style="position: absolute; right:10px; cursor: pointer" icon="trash-fill"></b-icon>
              </div>
            </b-list-group-item>
          </b-list-group>
        </b-collapse>
      </div>
      <div class="card-scene">
        <Container
          orientation="horizontal"
          @drop="onColumnDrop($event)"
          drag-handle-selector=".column-drag-handle"
          @drag-start="dragStart"
          :drop-placeholder="upperDropPlaceholderOptions"
        >
          <Draggable v-for="(column, bagIndex) in board.bags" :key="column.id" :ref=bagTagId(column.id)> <!-- todo COLUMNs -->
            <div>
              <div class="card-container">
                <div class="card-column-header column-drag-handle"> <!--todo note: column-drag-handle: make cursor move-->
                  <b-icon
                    v-b-tooltip.hover title="edit"
                    @click="showEditBagPopUp(column.id, bagIndex, column.name)"
                    variant="transparent"
                    class="ml-auto edit-column-icon"
                    icon="pencil-square"
                    font-scale="2">
                  </b-icon>
                  {{ column.name }}

                </div>
                <div style=" max-height:65vh; overflow-y: auto"> <!--todo COLUMN-->
                  <!--<div style="overflow-y: auto"> -->
                  <Container
                    group-name="col"
                    @drop="(e) => onCardDrop(column.id, e)"
                    @drag-start="(e) => log('drag start', e)"
                    @drag-end="(e) => log('drag end', e)"
                    :get-child-payload="getCardPayload(column.id)"
                    drag-class="card-ghost"
                    drop-class="card-ghost-drop"
                    :drop-placeholder="dropPlaceholderOptions"
                  >
                    <Draggable v-for="(card, cardIndex) in column.cards" :key="card.id" :ref="cardTagId(card.id)"> <!-- todo : CARDs -->
                      <div class="card" :style="makeColor(card.id)"> <!-- todo: CARD -->
                        <b-icon
                          v-b-tooltip.hover title="edit"
                          @click="showEditCardPopUp(card.id, cardIndex, bagIndex, card.name)"
                          variant="secondary"
                          class="ml-auto edit-card-icon"
                          icon="pencil-square"
                          font-scale="1.5">
                        </b-icon>

                        <p>{{ card.name }}</p>
                      </div>
                    </Draggable>
                  </Container>
                </div>
              </div>

              <creator-pop-up @onSubmit="createCard($event, column.id)"></creator-pop-up> <!-- create card-->
            </div>
          </Draggable>

          <creator-pop-up @onSubmit="createBag"></creator-pop-up> <!-- create bag: truyen ten ham cho -->
        </Container>
      </div>
      <bag-pop-up v-if="getBagPopupData.show" :data="getBagPopupData" @onSave="editBagName" @onDelete="deleteBag" @onCancel="closeEditBagPopUp"></bag-pop-up>
      <card-pop-up v-if="getCardPopupData.show" :data="getCardPopupData" @onSave="editCardName" @onCancel="closeEditCardPopUp" @onDelete="deleteCard"></card-pop-up>
    </div>
  </div>
</template>

<script>
  import {Container, Draggable} from 'vue-smooth-dnd'
  import {applyDrag} from '../../util/Helpers'
  import script from "./Board";
  import BoardNetwork from './BoardNetwork';
  import BagPopUp from './components/BagPopUp';
  import CardPopUp from './components/CardPopUp';
  import CreatorPopUp from './components/CreatorPopUp';
  import MyWebsocket from "../../network/MyWebsocket";
  import NetworkConfig from "../../config/NetworkConfig.js";
  import storage from '../../storage/LocalPersistence.js';
  import BoardWebsocketHandler from "./BoardWebsocketHandler.js";
  import BoardErrorHandler from "./BoardErrorHandler.js";
  import RouterUtil from "../../router/RouterUtil";

  const cardColors = [
    'azure',
    'beige',
    'bisque',
    'blanchedalmond',
    'burlywood',
    'cornsilk',
    'gainsboro',
    'ghostwhite',
    'ivory',
    'khaki'
  ]

  const pickColor = () => {
    const rand = Math.floor(Math.random() * 10)
    return cardColors[rand]
  };

  const board = {
    bags: []
  };

  // const board = {
  //   bags: [
  //     {
  //       id: "1",
  //       name: "column 1 nek",
  //       position: 0.0,
  //
  //       cards: [
  //         {
  //           id: 10,
  //           name: "card name",
  //           position: 1.1,
  //           bagId: 1
  //         }
  //       ]
  //     }
  //   ]
  // };
  export default {
    name: "Board.vue",

    components: {Container, Draggable, BagPopUp, CardPopUp, CreatorPopUp},

    beforeMount() {
      this.fetchAllBags();
      this.fetchAllMembers();
    },

    mounted() {
      this.connectWebsocket();
    },

    beforeDestroy() {
      this.boardWS.close();
    },

    data() {
      let self = this;
      return {
        boardId: self.$route.params.boardId,
        boardName: "",
        newName: "",
        board: board,
        bagById: {},
        cardById: {},
        cardColors: cardColors,
        upperDropPlaceholderOptions: {
          className: 'cards-drop-preview',
          animationDuration: '150',
          showOnTop: true
        },
        dropPlaceholderOptions: {
          className: 'drop-preview',
          animationDuration: '150',
          showOnTop: true
        },
        newBag: {
          position : 0.0,
          name: "default name",
          boardId: self.$route.params.boardId
        },

        boardNetwork: new BoardNetwork(self),

        boardWS : new MyWebsocket(NetworkConfig.WS_URL, this),

        WSHandler : new BoardWebsocketHandler(this),

        BoardErrorHandler: new BoardErrorHandler(this),

        addedEmail: "",
        showAddMemberInput: false,
        loading: 0,
        loadingItem: 2,

        widget: {
          sidebar_btn: {
            variant: "outline-secondary",
            delete_board_icon: "x-circle"
          },
          members_btn: {
            icon: "people-fill"
          },
          add_member_btn: {
            icon: "person-plus-fill"
          }
        },

        popUpBag: {
          show: false,
          value: null,
          bagId: -1,
          index: -1,
          rect: null
        },

        popUpCard: {
          show: false,
          value: null,
          cardId: -1,
          cardIndex: -1,
          bagIndex: -1,
          rect: null
        },

        currentUser: null,
        members: []
      }
    },

    computed: {
      getBagPopupData: function() {
        this.log("Board -> getBagPopupData " + JSON.stringify(this.popUpBag));
        let self = this;
        return {
          show: self.popUpBag.show,
          value: self.popUpBag.value,
          bagId: self.popUpBag.bagId,
          index: self.popUpBag.index,
          rect: self.popUpBag.rect
        };
      },

      getCardPopupData: function() {
        this.log("Board -> getCardPopupData " + JSON.stringify(this.popUpCard));
        let self = this;
        return {
          show: self.popUpCard.show,
          value: self.popUpCard.value,
          cardId: self.popUpCard.cardId,
          cardIndex: self.popUpCard.cardIndex,
          bagIndex: self.popUpCard.bagIndex,
          rect: self.popUpCard.rect
        };
      }
    },

    methods: {

      connectWebsocket: function() {
        let jwt = storage.getJwt();
        this.boardWS.setJwt(jwt);
        this.boardWS.connect();
      },

      fetchAllMembers: function () {
        let boardId = this.$route.params.boardId;
        let self = this;
        script.fetchAllMember(boardId,
          (response) => {
            console.log("Board -> fetchAllMembers " + boardId + " | " + JSON.stringify(response));
            let res = response.data;
            let mapMembers = res.members;
            let currentUser = mapMembers[res.reqId];

            self.currentUser = currentUser;
            self.members = Object.values(res.members);
            console.log("Board -> fetchAllMembers " + JSON.stringify(self.members));
          },
          self.BoardErrorHandler.onError.bind(self.BoardErrorHandler)
        );
      },

      addMemberByEmail() {
        this.ensureOpenedWS();
        let boardId = this.$route.params.boardId;
        let self = this;
        self.showAddMemberInput = !self.showAddMemberInput;
        script.addMemberByEmail(boardId, self.addedEmail,
          (response) => {
            console.log("Board -> addMemberByEmail " + JSON.stringify(response));
          },
          self.BoardErrorHandler.onError.bind(self.BoardErrorHandler)
        );
        self.addedEmail = "";
      },

      removeMember(memberId) {
        this.ensureOpenedWS();
        let self = this;
        script.removeMember(self.boardId, memberId,
          (response) => {
            console.log("Board -> removeMember " + JSON.stringify(response));
          },
          self.BoardErrorHandler.onError.bind(self.BoardErrorHandler)
        )
      },

      fetchAllBags: function () {
        let self = this;
        let boardId = this.$route.params.boardId;
        script.fetchBags(boardId,
          self.boardNetwork.fetchBagsOnSuccess.bind(self.boardNetwork),
          (error) => self.BoardErrorHandler.onError(error, self.boardNetwork.fetchBagsOnFail.bind(self.boardNetwork))
        );
      },

      createBag: function(name) {
        this.ensureOpenedWS();
        let self = this;
        this.newBag.position = this.board.bags && this.board.bags.length > 0 ? this.board.bags[this.board.bags.length - 1].position + 65536.0 : 0.0;
        this.newBag.name = name;

        console.log("Bags -> createBag: request - " + JSON.stringify(this.newBag));
        script.createBag(this.newBag,
          self.boardNetwork.createBagOnSuccess.bind(self.boardNetwork),
          self.BoardErrorHandler.onError.bind(self.BoardErrorHandler)
        );
      },

      showEditBagPopUp: function(bagId, bagIndex, bagName) {
        console.log("showEditBagPopUp bag " + bagId);
        this.popUpBag.rect = this.getBagRect(bagId);
        this.popUpBag.bagId = bagId;
        this.popUpBag.value = bagName;
        this.popUpBag.index = bagIndex;
        this.popUpBag.show = true;
      },

      closeEditBagPopUp: function(columnId) {
        console.log("closeEditBagPopUp bag " + columnId);
        this.popUpBag.show = false;
      },

      editBagName: function(bagId, editedName) {
        this.ensureOpenedWS();
        console.log("edit bagName " + bagId + " - " + editedName);
        let self = this;
        let data = {
          bagId: bagId,
          name: editedName
        };
        script.editBag(data,
          (response) => {
            self.log("Board -> editBagName success ", JSON.stringify(response));
            self.popUpBag.show = false;
          },
          self.BoardErrorHandler.onError.bind(self.BoardErrorHandler)
        );
      },

      getBagRect: function(bagId) {
        let bag_Id = this.bagTagId(bagId);
        let bagRef = this.$refs[bag_Id];
        let rect = bagRef[0].$el.getBoundingClientRect();
        this.log(bag_Id, bagRef, rect);
        return rect;
      },

      deleteBag: function(bagId, index) {
        this.ensureOpenedWS();
        console.log("delete bag " + bagId + " at " + index);
        this.popUpBag.show = false;
        let self = this;
        script.deleteBag(bagId,
          (response) => {
            console.log("[SUCCESS] delete bag " + bagId + " at " + index);
            // self.board.bags.splice(index, 1);
            // delete self.bagById[bagId]; // remove OneToOne
          },
          self.BoardErrorHandler.onError.bind(self.BoardErrorHandler)
        )
      },

      moveBag: function(bagId, newPosition) {
        this.ensureOpenedWS();
        this.log("Board -> moveBag", bagId, newPosition);
        let self = this;
        script.moveBag(bagId, newPosition,
          (response) => {
            self.log("Board -> moveBag success", bagId, newPosition, JSON.stringify(response));
            // let res = response.data;
            // let bag = self.bagById[res.bagId];
            // bag.position = res.position;
          },
          self.BoardErrorHandler.onError.bind(self.BoardErrorHandler)
        )
      },

      /**
       * ======================== CARD
       */

      fetchAllCards: function() {
        let self = this;
        this.board.bags.forEach(bag => {
          self.fetchCards(bag.id)
        })
      },

      fetchCards(bagId) {
        let self = this;
        script.fetchCards(bagId,
          self.boardNetwork.fetchCardOnSuccess.bind(self.boardNetwork),
          self.boardNetwork.fetchCardOnError.bind(self.boardNetwork)
        )
      },

      createCard(name, bagId) {
        this.ensureOpenedWS();
        this.log("board -> createCard", name, bagId);
        let cards = this.bagById[bagId].cards;
        let position = (cards.length == 0) ? 65536.0 : cards[cards.length - 1].position + 65536.0;
        let newCard = {
          name: name,
          position: position,
          bagId: bagId
        };
        let self = this;
        script.createCard(newCard,
          self.boardNetwork.createCardOnSuccess.bind(self.boardNetwork),
          self.BoardErrorHandler.onError.bind(self.BoardErrorHandler)
        )
      },

      editCardName(cardId, name) {
        this.ensureOpenedWS();
        this.log("edit card ", cardId, name);
        let self = this;
        let data = {
          cardId: cardId,
          name: name
        };
        script.editCard(data,
          (response) => {
            self.log("Board -> editBagName success ", JSON.stringify(response));
            // self.cardById[cardId].name = name;
            self.popUpCard.show = false;
          },
          self.BoardErrorHandler.onError.bind(self.BoardErrorHandler)
        )
      },

      showEditCardPopUp: function(cardId, cardIndex, bagIndex, cardName) {
        this.log("showEditCardPopUp card ", cardId, cardIndex, bagIndex, cardName);
        this.popUpCard.rect = this.getCardRect(cardId);
        this.popUpCard.cardId = cardId;
        this.popUpCard.value = cardName;
        this.popUpCard.cardIndex = cardIndex;
        this.popUpCard.bagIndex = bagIndex;
        this.popUpCard.show = true;
      },

      closeEditCardPopUp: function(cardId) {
        console.log("closeEditCardPopUp card " + cardId);
        this.popUpCard.show = false;
      },

      deleteCard(cardId, bagIndex, cardIndex) {
        this.ensureOpenedWS();
        console.log("delete bag " + cardId + " at " + cardIndex);
        let self = this;
        script.deleteCard(cardId,
          (response) => {
            console.log("[SUCCESS] delete card " + cardId + " at " + cardIndex);
            // self.board.bags[bagIndex].cards.splice(cardIndex, 1);
            // delete self.cardById[cardId]; // remove OneToOne
          },
          self.BoardErrorHandler.onError.bind(self.BoardErrorHandler)
        )
      },

      getCardRect: function(cardId) {
        let cardTagId = this.cardTagId(cardId);
        let cardRef = this.$refs[cardTagId];
        let rect = cardRef[0].$el.getBoundingClientRect();
        this.log(cardTagId, cardRef, rect);
        return rect;
      },

      moveCard: function(cardId, toBagId, newPosition) {
        this.ensureOpenedWS();
        this.log("Board -> moveCard", cardId, toBagId, newPosition);
        let self = this;
        let data = {
          cardId: cardId,
          toBagId: toBagId,
          newPosition: newPosition
        };
        script.moveCard(data,
          (response) => {
            console.log("moveCard success " + JSON.stringify(response));
            // let card = self.cardById[cardId];
            // card.position = response.data.position;
          },
          self.BoardErrorHandler.onError.bind(self.BoardErrorHandler)
        )
      },

      /**
       *  ========================
       */
      makeColor: function(id) {
        let self = this;
        return {
          backgroundColor: self.cardColors[id % self.cardColors.length]
        }
      },

      bagTagId: function(bagId) {
        return "bag-" + bagId;
      },

      cardTagId: function(cardId) {
        return "card-" + cardId;
      },

      /**
       * ===================
       */

      refTagId: function(prefix, id) {
        return prefix + "-" + id;
      },

      /**
       * ==========================================
       */

      /**
       *
       * @param dropResult: {"removedIndex":3,"addedIndex":1}
       */
      onColumnDrop(dropResult) {
        console.log("onColumnDrop " + JSON.stringify(dropResult));

        const scene = Object.assign({}, this.board)
        scene.bags = applyDrag(scene.bags, dropResult)
        this.board = scene;

        // req to move
        let addedIndex = dropResult.addedIndex;
        let bag = this.board.bags[addedIndex];
        if (bag.length == 1) return;

        let newPosition = this.getReqBagPosition(addedIndex);
        bag.position = newPosition;

        this.moveBag(bag.id, newPosition);
      },

      getReqBagPosition(addedIndex) {
        let bag = this.board.bags[addedIndex];

        let size = this.board.bags.length;
        if (size <= 1) return bag.position;

        let oldPosition = bag.position;
        let newPosition = oldPosition;

        if (addedIndex == 0) {
          newPosition = (0.0 + this.board.bags[1].position) / 2.0;
        }else if (addedIndex == size - 1) {
          newPosition = this.board.bags[size - 2].position + 65536.0;
        } else {
          newPosition = (this.board.bags[addedIndex - 1].position + this.board.bags[addedIndex + 1].position) / 2.0;
        }
        return newPosition;
      },

      /**
       * {"removedIndex":0,"addedIndex":1,"payload":{"id":69,"bagId":49,"name":"dsf","position":32767.5}}
       * @param columnId
       * @param dropResult
       */
      onCardDrop(columnId, dropResult) {
        if (dropResult.removedIndex !== null || dropResult.addedIndex !== null) {
          this.log(" onCardDrop ", JSON.stringify(dropResult), columnId);
          const scene = Object.assign({}, this.board)
          const column = scene.bags.filter(p => p.id === columnId)[0]
          const columnIndex = scene.bags.indexOf(column)

          const newColumn = Object.assign({}, column)
          newColumn.cards = applyDrag(newColumn.cards, dropResult);
          scene.bags.splice(columnIndex, 1, newColumn);

          this.board = scene;
          this.bagById[newColumn.id] = newColumn;
        }

        if (dropResult.addedIndex != null) {
          let newPosition = this.getReqCardPosition(dropResult, columnId);
          let cardId = dropResult.payload.id;
          let toBagId = columnId;
          this.moveCard(cardId, toBagId, newPosition);
        }
      },

      getReqCardPosition(dropResult, columnId) {
        let newIndex = dropResult.addedIndex;
        let toBagId = columnId;

        let bag = this.bagById[toBagId];
        let card = bag.cards[newIndex];
        let newPosition;

        if (bag.cards.length == 1) {
          newPosition = 65536.0;
        } else if (newIndex == 0) {
          newPosition = (0.0 + bag.cards[1].position) / 2.0;
        } else if (newIndex == bag.cards.length - 1) {
          newPosition = bag.cards[newIndex - 1].position + 65536.0;
        } else {
          newPosition = (bag.cards[newIndex - 1].position + bag.cards[newIndex + 1].position) / 2.0;
        }

        card.position = newPosition;
        return newPosition;
      },

      getCardPayload(columnId) {
        return index => {
          return this.board.bags.filter(p => p.id === columnId)[0].cards[index]
        }
      },

      dragStart() {
        console.log('drag started')
      },

      log(...params) {
        console.log(...params)
      },

      /**
       * =========== BOARD
       */

      deleteBoard: function() {
        this.ensureOpenedWS();
        this.log("Board -> deleteBoard: ", this.boardId);
        let self = this;
        script.deleteBoard(this.boardId,
          (response) => {

          },
          self.BoardErrorHandler.onError.bind(self.BoardErrorHandler)
        );
      },

      updateBoard: function() {
        this.ensureOpenedWS();
        this.log("Board -> updateBoard: ", this.boardId, this.newName);
        let self = this;
        script.updateBoard(this.boardId, this.newName,
          (response) => {

          },
          self.BoardErrorHandler.onError.bind(self.BoardErrorHandler)
        );
      },

      /**
       * =========== Websocket
       */

      onopen(event) {
        this.loading += 1;
      },

      onmessage(event) {
        this.WSHandler.onmessage(event);
      },

      onclose(event) {
        this.WSHandler.onclose(event);
      },

      onerror(error) {
        this.WSHandler.onerror(error);
      },

      ondie(event) {
        this.WSHandler.onclose(event);
      },

      ensureOpenedWS() {
        if (this.boardWS.getState() != 'OPEN') {
          RouterUtil.reload(this);
        }
      }

    }
  }
</script>

<style scoped>

  .edit-card-icon {
    cursor: pointer;
  }

  .edit-card-icon:hover {

  }

  .card {
    cursor: move;
  }
  .card:hover {
    /*cursor: grab;*/
  }

  .add-card-btn, .add-bag-btn {

    width: 320px;
    margin: 5px;
