import WsCmd from '../../config/WebsocketCmd.js';

/**
 *
 * @author thai-van
 *
 **/

class HomeWebsocketHandler {

  constructor(page) {
    this.page = page;
    this.buildHandler();
  }

  buildHandler() {
    this.handlers = {};

    this.handlers[WsCmd.LEAVE_BOARD]     = this.receiveMessageLeaveBoard.bind(this);
    this.handlers[WsCmd.ADD_MEMBER]      = this.receiveMessageAddMember.bind(this);
    this.handlers[WsCmd.DELETE_BOARD]    = this.receiveMessageDeleteBoard.bind(this);
  }

  onmessage(event) {
    console.log(`HomeWebsocketHandler -> onMessage ${event.data}`);
    let data = JSON.parse(event.data);
    this.handlers[data.action] && this.handlers[data.action](data);
    !this.handlers[data.action] && this.commonMessageHandler(data);
  }

  onclose(event) {

  }

  onerror(error) {

  }

  receiveMessageLeaveBoard(response) {

    let memberId = response.message;

    let title = "Leave board " + response.boardId;
    let msg;
    let color;

    if (this.page.currentUserId == memberId) {
      msg = "You leave the board " + response.boardId;
      color = "warning";

      for (let i = 0; i < this.page.boards.length; i++) {
        if (this.page.boards[i].id == response.boardId) {
          this.page.boards.splice(i, 1);
          break;
        }
      }
    } else {
      msg = "UserId " + memberId + " leave the board " + response.boardId;
      color = "info";
    }

    this.onCreateNotification(title, msg, color);
  }

  receiveMessageDeleteBoard(response) {

    for (let i = 0; i < this.page.boards.length; i++) {
      if (this.page.boards[i].id == response.boardId) {
        this.page.boards.splice(i, 1);
        break;
      }
    }

    let title = "Delete board " + response.boardId;
    let msg = "Board " + response.boardId + " is deleted!!";
    let color = "warning";
    this.onCreateNotification(title, msg, color);
  }

  receiveMessageAddMember(response) {
    let memberId = response.message.id;

    if (memberId == this.page.currentUserId) {
      const h = this.page.$createElement;
      let title = "You are added into boardId " + response.boardId;
      let message = h('b-link', {props: {href: '/board/' + response.boardId}}, "CLICK TO SEE");
      this.onCreateNotification(title, message);
    } else {
      this.commonMessageHandler(response);
    }

  }

  commonMessageHandler(response) {
    let title = response.action.toUpperCase() + " in boardId " + response.boardId;
    const h = this.page.$createElement;
    let message = h('b-link', {props: {href: '/board/' + response.boardId}}, "CLICK TO SEE");
    this.onCreateNotification(title, message);
  }

  onCreateNotification(title, msg, variant = "info") {
    console.log("homeWS -> onCreateNotification: " + title + " | " + msg);
    const h = this.page.$createElement;
    let message =
      h(
        'div',
        [
          h('b-spinner', { props: { type: 'grow', small: true } }),
          msg,
          h('b-spinner', { props: { type: 'grow', small: true } }),
        ]
      );
    this.page.$bvToast.toast(
      [message],
      {
        variant: variant,
        title: title,
        autoHideDelay: 5000,
        appendToast: false
      }
    );
  }
}

export default HomeWebsocketHandler;
