import STORAGE_KEY from "./StorageConstant"

class LocalPersistence {

  constructor() {}

  clearJwt() {
    localStorage.removeItem(STORAGE_KEY.JWT);
  }

  getJwt() {
    return localStorage.getItem(STORAGE_KEY.JWT);
  }

  setJwt(jwt, callback) {
    console.log("LocalStorage -> setJwt :: " + STORAGE_KEY.JWT + " -> " + jwt);
    localStorage.setItem(STORAGE_KEY.JWT, jwt);
    callback && callback();
  }

  setSocialJwt(jwt, callback) {
    console.log("LocalStorage -> setSocialJwt :: " + STORAGE_KEY.FIREBASE_JWT + " -> " + jwt);
    localStorage.setItem(STORAGE_KEY.FIREBASE_JWT, jwt);
    callback && callback();
  }

  getSocialJwt() {
    return localStorage.getItem(STORAGE_KEY.FIREBASE_JWT);
  }

  clearSocialJwt() {
    localStorage.removeItem(STORAGE_KEY.FIREBASE_JWT);
  }

}

const INSTANCE = new LocalPersistence();
export default INSTANCE;
