<template>
  <div>
    <b-navbar style="background-color: #f8f9fa;"  type="light">
      <b-navbar-nav v-for="endpoint in endpoints" :key="endpoint.url">
        <b-nav-item v-if="endpoint.alwaysShow || logged_in == endpoint.showOnLogin" :href="endpoint.url">
          {{endpoint.name}}
        </b-nav-item>
      </b-navbar-nav>

      <!--<b-nav-form>-->
        <!--<b-form-input size="sm" class="mr-sm-2" placeholder="Search"></b-form-input>-->
        <!--<b-button size="sm" class="my-2 my-sm-0" type="submit">Search</b-button>-->
      <!--</b-nav-form>-->

      <!-- Right aligned nav items -->
      <b-navbar-nav class="ml-auto">
        <b-button v-if="logged_in" @click="logout" variant="outline-secondary" class="mb-2">
          <b-icon icon="power"></b-icon> Logout
        </b-button>
      </b-navbar-nav>
    </b-navbar>

  </div>
</template>

<script>
  import firebase from 'firebase/app';
  import 'firebase/auth';

  import storage from '../storage/LocalPersistence';

  export default {
    name: "Nav",
    data() {
      let self = this;
      return {
        endpoints: {
          HOME: {
            url: "/",
            name: "Home",
            showOnLogin: true
          },
          ABOUT: {
            url: "/about",
            name: "About",
            alwaysShow: true
          },
          SIGN_IN: {
            url: "/sign-in",
            name: "Sign-in",
            showOnLogin: false
          },
        }
      }
    },

    methods: {
      logout: function () {
        storage.clearJwt();
        let self = this;
        firebase.auth().signOut().then(
          () => {
            this.$router.replace(self.endpoints.SIGN_IN.url);
            // RouterUtil.redirectToHomeFrom(this);
          }
        )
      }
    },

    computed: {
      logged_in: function() {
        return storage.getJwt() != null;
      }
    }
  }
</script>

