import axios from 'axios';
import apiCaller from '../network/APICaller'
import REST_API from '../network/RestApiConstant'

class SignIn {
  constructor() {}

  basicSignIn(signInData, onSuccess, onError) {

    let data = {
      email: signInData.email,
      password: signInData.password
    };

    axios.create({});
    apiCaller.post(REST_API.BASIC_SIGN_IN, data, axios, onSuccess, onError);
  }

  basicSignUp(signInData, onSuccess, onError) {

    let data = {
      email: signInData.email,
      password: signInData.password,
      displayedName: signInData.displayedName
    };

    axios.create({});
    apiCaller.post(REST_API.BASIC_SIGN_UP, data, axios, onSuccess, onError);
  }

  socialLogin(jwt, onSuccess, onError) {
    let data = {
      tokenId: jwt
    };

    axios.create({});
    apiCaller.post(REST_API.SOCIAL_SIGN_IN, data, axios, onSuccess, onError);
  }

}

const instance = new SignIn();
export default instance;
