import storage from '../storage/LocalPersistence.js';
import RouterUtil from '../router/RouterUtil';
import ENDPOINT from "../router/endpoint.js";

class SignOut {

  constructor() {}

  signOut(page) {
    storage.clearJwt();
    RouterUtil.redirect(page, ENDPOINT.SIGN_IN);
  }

}

const instance = new SignOut();
export default instance;
