import httpStatus from '../../network/HttpStatus';
import RouterUtil from "../../router/RouterUtil";
import ENDPOINT from '../../router/endpoint';

class BoardNetwork {

  constructor(page) {
    this.page = page;
  }

  fetchBagsOnSuccess(response) {
    console.log("Bags -> fetchAllBags: " + JSON.stringify(response));
    let page = this.page;
    let status = response.status;
    if (status == httpStatus.OK) {
      page.board.bags = [];
      this.page.boardName = response.data.boardName;
      // this.page.newName = response.data.boardName;
      let bags = response.data.bags;
      let mapBags = bags.map(bag => {
        let mapBag = {
          id: bag.bagId,
          name: bag.name,
          position: bag.position,
          cards: []
        };
        page.bagById[mapBag.id] = mapBag; // map oneToOne
        return mapBag;
      });
      page.board.bags = mapBags;
      console.log("Bags -> fetchAllBags. Message: " + JSON.stringify(page.board.bags));

      page.loadingItem += page.board.bags.length;
      page.loading += 1;
      page.fetchAllCards();
    } else {
      RouterUtil.redirect(page, ENDPOINT.NOT_FOUND);
    }
  }

  createBagOnSuccess(response) {
    // console.log("Bags -> createBag: " + JSON.stringify(response));
    // let newBag = response.data;
    // let bag = {
    //   id: newBag.bagId,
    //   name: newBag.name,
    //   position: newBag.position,
    //   cards: []
    // };
    // this.page.board.bags.push(bag);
    // this.page.bagById[bag.id] = bag; // map oneToOne
    // this.page.board.bags.sort((thisBag, thatBag) => {return thisBag.position - thatBag.position;});
    //
    // console.log("Bags -> createBag: onSuccess: " + JSON.stringify(this.page.board.bags));
  }

  createBagOnError(error) {
    console.log("Bags -> createBag. error " + JSON.stringify(error));
  }

  fetchBagsOnFail(error) {
    console.log("Bags -> fetchAllBags. error " + JSON.stringify(error));
    RouterUtil.redirect(this.page, ENDPOINT.NOT_FOUND);
  }

  fetchCardOnSuccess(response) {
    console.log("Cards -> fetchCardOnSuccess: " + JSON.stringify(response));
    let page = this.page;
    let status = response.status;
    if (status == httpStatus.OK) {
      page.loading += 1;

      let cards = response.data;
      if (cards.length == 0) return;
      let mapCards = cards.map(card => {
        let mapCard = {
          id: card.id,
          name: card.name,
          position: card.position,
          bagId: card.bagId
        };

        this.page.cardById[mapCard.id] = mapCard;
        return mapCard;
      });

      mapCards.sort((a, b) => { return a.position - b.position});

      page.board.bags.forEach(bag => {
        if (bag.id == cards[0].bagId) {
          bag.cards = mapCards;
          console.log("Cards -> fetchCardOnSuccess: data board " + JSON.stringify(page.board.bags));
          return;
        }
      });

    }
  }

  fetchCardOnError(error) {

  }

  createCardOnSuccess(response) {
    console.log("Cards -> createCardOnSuccess: " + JSON.stringify(response));
    // let newCard = response.data;
    // this.page.cardById[newCard.id] = newCard;
    // this.page.board.bags.forEach(bag => {
    //   if (bag.id == newCard.bagId) {
    //     bag.cards.push(newCard);
    //     bag.cards.sort((c1, c2) => {
    //       return c1.position - c2.position;
    //     });
    //     console.log("Cards -> createCardOnSuccess: " + JSON.stringify(this.page.board.bags));
    //     return;
    //   }
    // });
  }

  createCardOnError(error) {
    console.log("Cards -> createCardOnError: " + JSON.stringify(error));
  }
}

export default BoardNetwork;
