import ENDPOINT from "./endpoint"

export default {

  redirectToHomeFrom(from) {
    this.redirect(from);
  },

  redirect: function (from, to) {
    to = to || ENDPOINT.HOME;
    from.$router.replace(to);
  },

  reload: function(page) {
    page.$router.go();
  }
}
