/**
 *
 * All events that MyWebsocket#socket receives
 * should be handled by delegate's functions:
 * <ul>
 *     <li>onopen</li>
 *     <li>onmessage</li>
 *     <li>onclear</li>
 *     <li>ondie</li>
 *     <li>onerror</li>
 * </ul>
 * @author thai-van
 *
 **/
class MyWebsocket {

  constructor(url, delegate, jwt) {
    this.url = url;
    this.delegate = delegate;
    this.socket = null;
    this.jwt = jwt;
  }

  connect() {
    let self = this;

    this.socket       = new WebSocket(this.url +"?jwt=" + this.jwt);
    let socket        = this.socket;

    socket.onopen     = self.onopen.bind(self);
    socket.onmessage  = self.onmessage.bind(self);
    socket.onclose    = self.onclose.bind(self);
    socket.onerror    = self.onerror.bind(self);
  }

  setJwt(jwt) {
    this.jwt = jwt;
  }

  send(message) {
    this.socket.send(message);
  }

  onopen(e) {
    console.log('[open] Connection established');
    this.delegate.onopen && this.delegate.onopen(e);
  }

  onmessage(event) {
    console.log(`[message] Data received from server: ${event.data}`);
    this.delegate.onmessage && this.delegate.onmessage(event);
  }

  onclose(event) {
    if (event.wasClean) {
      console.log(`[close] Connection closed cleanly, code=${event.code} reason=${event.reason}`);
      this.delegate.onclear && this.delegate.onclear(event);
      return;
    }

    // e.g. server process killed or network down
    // event.code is usually 1006 in this case
    console.log('[close] Connection died');
    this.delegate.ondie && this.delegate.ondie(event);

  }

  onerror(error) {
    console.log('[error] ' + error.message);
    this.delegate.onerror && this.delegate.onerror(error);
  }

  close() {
    this.socket.close();
  }

  getState() {
    let wsState = this.socket.readyState;
    switch (wsState) {
      case 0:
        return "CONNECTING";
      case 1:
        return "OPEN";
      case 2:
        return "CLOSING";
      case 3:
        return "CLOSED";
      default:
        return "undefined"
    }
  }
}

export default MyWebsocket;
