import storage from '../storage/LocalPersistence';
import header from './HeaderConstant';
import Webconfig from '../config/NetworkConfig';
class APICaller {

  constructor() {
    this.httpServer = Webconfig.HTTP_URL;
  }

  get(endpoint, axios, onSuccess, onFail) {
    let URL = this.httpServer + endpoint;
    let jwt = storage.getJwt();

    console.log("[GET]: " + URL);

    jwt && (axios.defaults.headers.common[header.AUTHEN_HEADER] = "Bearer " + jwt);
    axios.get(URL).then(onSuccess, onFail);
  }

  post(endpoint, data, axios, onSuccess, onFail) {
    let URL = this.httpServer + endpoint;
    let jwt = storage.getJwt();

    console.log("[POST]: " + URL);

    jwt && (axios.defaults.headers.common[header.AUTHEN_HEADER] = "Bearer " + jwt);
    axios.post(URL, data).then(onSuccess, onFail);
  }

  put(endpoint, data, axios, onSuccess, onFail) {
    let URL = this.httpServer + endpoint;
    let jwt = storage.getJwt();

    console.log("[PUT]: " + URL);

    jwt && (axios.defaults.headers.common[header.AUTHEN_HEADER] = "Bearer " + jwt);
    axios.put(URL, data).then(onSuccess, onFail);
  }

  delete(endpoint, axios, onSuccess, onFail) {
    let URL = this.httpServer + endpoint;
    let jwt = storage.getJwt();

    console.log("[DELETE]: " + URL);

    jwt && (axios.defaults.headers.common[header.AUTHEN_HEADER] = "Bearer " + jwt);
    axios.delete(URL).then(onSuccess, onFail);
  }

}

const instance = new APICaller();
export default instance;
