<template>
  <div id="app">
    <Nav v-if="visible_nav"></Nav>

    <div class="app demo">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  import ENDPOINT from "./router/endpoint"
  import Nav from "./components/Nav";
  export default {
    name: "App",
    components: {Nav},
    computed: {
      visible_nav: function () {
        return this.$route.path !== ENDPOINT.SIGN_IN && this.$route.path != ENDPOINT.NOT_FOUND;
      }
    }
  }
</script>

