import axios from 'axios';
import apiCaller from '../../network/APICaller'
import REST_API from '../../network/RestApiConstant'

class Home {
  constructor() {}

  mapToBoards(resBoards) {
    let self = this;
    return resBoards.map((board) => {
      return self.mapToBoard(board);
    })
  }

  mapToBoard(rBoard) {
    return {
      id: rBoard.id,
      name: rBoard.name,
      role: rBoard.role
    }
  }

  createBoard(board, onSuccess, onError) {

    let data = {
      boardName: board.boardName
    };

    axios.create({});
    apiCaller.post(REST_API.BOARD, data, axios, onSuccess, onError);
  }

  fetchBoards(onSuccess, onError) {
    axios.create({});
    apiCaller.get(REST_API.BOARD, axios, onSuccess, onError);
  }
}

const instance = new Home();
export default instance;
