import Vue from 'vue'
import VueRouter from 'vue-router'
import 'firebase/auth';

import Home from '../views/home/Home.vue'
import SignIn from '../views/SignIn.vue'
import About from '../views/About.vue'
import Board from '../views/board/Board.vue'
import NotFound from '../views/NotFound.vue'

import ENDPOINT from './endpoint'

import storage from '../storage/LocalPersistence';

Vue.use(VueRouter);

const routes = [
  {
    path: ENDPOINT.HOME,
    name: 'Home',
    component: Home,
    meta: {
      requiresAuth: true
    }
  },

  {
    path: ENDPOINT.BOARD + "/:boardId",
    name: 'Board',
    component: Board,
    meta: {
      requiresAuth: true
    }
  },

  {
    path: ENDPOINT.ABOUT,
    name: 'About',
    component: About,
    meta: {
      requiresAuth: false
    }
  },
  {
    path: ENDPOINT.SIGN_IN,
    name: 'Sign In',
    component: SignIn
  },

  {
    path: ENDPOINT.NOT_FOUND,
    name: '404',
    component: NotFound
  }
];

const router = new VueRouter({
  mode: 'history',
  // base: process.env.BASE_URL,
  // base: ENDPOINT.BASE,
  routes
});

router.beforeEach((to, from, next) => {
  const jwt = storage.getJwt();
  const requiresAuth = to.matched.some(record => record.meta.requiresAuth);

  if (requiresAuth && !jwt) {
    next(ENDPOINT.SIGN_IN);
  }
  else if (jwt && to.path==ENDPOINT.SIGN_IN) next(ENDPOINT.HOME);
  else next();
});

export default router
