import axios from 'axios';
import apiCaller from '../../network/APICaller'
import REST_API from '../../network/RestApiConstant'

class Board {
  constructor() {

  }

  fetchBags(boardId, onSuccess, onError) {
    let url = REST_API.BAG + "/board-id/" + boardId;
    axios.create({});
    apiCaller.get(url, axios, onSuccess, onError);
  }

  createBag(bag, onSuccess, onError) {
    let url = REST_API.BAG;
    let data = {
      name: bag.name,
      position: bag.position,
      boardId: bag.boardId
    };
    axios.create({});
    apiCaller.post(url, data, axios, onSuccess, onError);
  }

  deleteBag(bagId, onSuccess, onError) {
    let url = REST_API.BAG + "/" + bagId;
    axios.create({});
    apiCaller.delete(url, axios, onSuccess, onError);
  }

  editBag(changedData, onSuccess, onError) {
    let url = REST_API.BAG;
    let data = {
      id: changedData.bagId,
      name: changedData.name
    };
    axios.create({});
    apiCaller.put(url, changedData, axios, onSuccess, onError);
  }

  moveBag(bagId, newPosition, onSuccess, onError) {
    let url = REST_API.BAG + "/" + bagId + "/" + newPosition;
    let data = {};
    axios.create({});
    apiCaller.put(url, data, axios, onSuccess, onError);
  }

  // ========================

  fetchCards(bagId, onSuccess, onError) {
    let url = REST_API.CARD + "/bag-id/" + bagId;
    axios.create({});
    apiCaller.get(url, axios, onSuccess, onError);
  }

  createCard(card, onSuccess, onError) {
    let url = REST_API.CARD;
    let data = {
      bagId: card.bagId,
      name: card.name,
      position: card.position
    };
    axios.create({});
    apiCaller.post(url, data, axios, onSuccess, onError);
  }

  deleteCard(cardId, onSuccess, onError) {
    let url = REST_API.CARD + "/" + cardId;
    axios.create({});
    apiCaller.delete(url, axios, onSuccess, onError);
  }

  editCard(changedData, onSuccess, onError) {
    let url = REST_API.CARD;
    let data = {
      id: changedData.cardId,
      name: changedData.name
    };
    axios.create({});
    apiCaller.put(url, changedData, axios, onSuccess, onError);
  }

  moveCard(data, onSuccess, onError) {
    let url = REST_API.CARD + "/move";
    let req = {
      cardId: data.cardId,
      toBagId: data.toBagId,
      position: data.newPosition
    };

    axios.create({});
    apiCaller.put(url, req, axios, onSuccess, onError);
  }

  fetchAllMember(boardId, onSuccess, onError) {
    let url = REST_API.BOARD + "/" + boardId + "/members";
    axios.create({});
    apiCaller.get(url, axios, onSuccess, onError);
  }

  addMemberById(boardId, uId, onSuccess, onError) {
    let url = REST_API.BOARD + "/" + boardId + "/member-id/" + uId;
    axios.create({});
    apiCaller.post(url, {}, axios, onSuccess, onError);
  }

  addMemberByEmail(boardId, email, onSuccess, onError) {
    let url = REST_API.BOARD + "/" + boardId + "/member-email/" + email;
    console.log("Board.js -> addMemberByEmail. url " + url);
    axios.create({});
    apiCaller.post(url, {}, axios, onSuccess, onError);
  }

  removeMember(boardId, memberId, onSuccess, onError) {
    let url = REST_API.BOARD + "/" + boardId + "/member-id/" + memberId;
    console.log("Board.js -> removeMember. url " + url);
    axios.create({});
    apiCaller.delete(url, axios, onSuccess, onError);
  }

  /**
   * ========= BOARD
   */

  deleteBoard(boardId, onSuccess, onError) {
    let url = REST_API.BOARD + "/" + boardId;
    console.log("Board.js -> deleteBoard. url " + url);
    axios.create({});
    apiCaller.delete(url, axios, onSuccess, onError);
  }

  updateBoard(boardId, newName, onSuccess, onError) {
    let url = REST_API.BOARD + "/" + boardId;
    console.log("Board.js -> deleteBoard. url " + url);

    let data = {
      boardName: newName
    };

    axios.create({});
    apiCaller.post(url, data, axios, onSuccess, onError);
  }

}

const instance = new Board();
export default instance;
